import {
	requestBoardList,
	requestBoardDetail
} from "@/apis/board_api";

const state = {
	boardList: [],
	boardPage: null,
	boardPageTotal: 0,
	boardTotalElemtnts: 0,
	boardDetail: {
		boardTitle: '',
		boardContent: '',
		boardFile: [],
		regDate: new Date()
	}
};

const getters = {
	getBoardList(state) {
		return state.boardList;
	},
	getBoardPage(state) {
		if (state.boardPage === null) {
			return 0;
		} else {
			return state.boardPage.pageNumber + 1;
		}
	},
	getBoardPageTotal(state) {
		return state.boardPageTotal;
	},
	getBoardTotalElements(state) {
		return state.boardTotalElemtnts;
	},
	getBoardDetail(state) {
		return state.boardDetail
	}
};

const mutations = {
	BOARD_LIST(state, data) {
		state.boardList = data.resultBoardList
		state.boardPage = data.page
		state.boardPageTotal = data.pageTotal
		state.boardTotalElemtnts = data.totalElemtnts
	},
	BOARD_LIST_ADD(state, data) {
		state.boardList = [].concat.apply(state.boardList, data.resultBoardList);
		state.boardPage = data.page
		state.boardPageTotal = data.pageTotal
		state.boardTotalElemtnts = data.totalElemtnts
	},
	BOARD_DETAIL(state, data) {
		state.boardDetail = data.resultBoard
	},
	BOARD_INIT(state) {
		state.boardList = []
		state.boardPage = null
		state.boardPageTotal = 0
		state.boardTotalElemtnts = 0
		state.boardDetail = {
			boardTitle: '',
			boardContent: '',
			boardFile: [],
			regDate: new Date()
		}
	}
};

const actions = {
	async REQUEST_BOARD_LIST(context, parameter) {
		try {
			context.commit('START_LOADING');
			const response = await requestBoardList(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("BOARD_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async REQUEST_BOARD_DETAIL(context, parameter) {
		try {
			context.commit('START_LOADING');
			const response = await requestBoardDetail(parameter.id);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("BOARD_DETAIL", response.data);
				context.commit('END_LOADING');
				return true;
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
			return false;
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
			return false;
		}
	}
};

export default { mutations, state, actions, getters };