import axios from 'axios'

function requestRegistFormatFolder(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/format/reg`, parameter.formatType)
}

function requestRegistFormatFolderList(){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/format/list`)
}
function requestDeleteFormatFolder(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/format/delete`, parameter.formatType)
}
function requestChangeFormatFolderName(parameter){

	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/format/rename`, parameter.formatType)
}

function requestRegistFormatFolderInList(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/format/in-list`, parameter.formatType)
}


function requestRegistFormat(parameter){
	let form = new FormData();
	for(let i=0;i<parameter.files.length;i++){
	  form.append("fileList", parameter.files[i]);
	}
	const formParam = parameter.form
	let formParamString = JSON.stringify(formParam);
	form.append("formParamString",formParamString)
  
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/format/in-reg`,form,
		{
			headers:{
			'Content-Type' : 'multipart/form-data'
			}
		});
}

function requestDeleteFormat(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/format/in-delete`, parameter.form)
}

function requestFormatAllList(){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/format/in-alllst`)
}
export {
	requestRegistFormatFolder,
	requestRegistFormatFolderList,
	requestRegistFormatFolderInList,
	requestRegistFormat,
	requestDeleteFormatFolder,
	requestDeleteFormat,
	requestChangeFormatFolderName,
	requestFormatAllList
}
