import axios from 'axios'

function requestAddressSearch(parameter){
	const kakaoLocalAddressSearchUrl = 'https://dapi.kakao.com/v2/local/search/keyword.json'
	const kakaoLocalAddressSearchAccessToken = 'KakaoAK dad84450d0b9170e85df389b6f975410'
	return axios({
		method: 'get',
		url: `${kakaoLocalAddressSearchUrl}?page=1&size=15&query=${encodeURIComponent(parameter)}`,
		headers: {'Authorization': `${kakaoLocalAddressSearchAccessToken}`}
	  })
}

function requestAddressSearchPostCode(parameter){
	const kakaoLocalAddressSearchUrl = 'https://dapi.kakao.com/v2/local/geo/coord2address.json'
	const kakaoLocalAddressSearchAccessToken = 'KakaoAK dad84450d0b9170e85df389b6f975410'
	return axios({
		method: 'get',
		url: `${kakaoLocalAddressSearchUrl}?x=${parameter.x}&y=${parameter.y}&input_coord=WGS84`,
		headers: {'Authorization': `${kakaoLocalAddressSearchAccessToken}`}
	  })
}

function requestFuneralAddressSearch(parameter){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/funeralAddr/list?address=${parameter}`)
	
}
export {
	requestAddressSearch,
	requestAddressSearchPostCode,
	requestFuneralAddressSearch
}
