import {
	requestRegistCodeSuper,
	requestRegistCode,
	requestRegistCodeSub,
	requestUpdateCodeSuper,
	requestUpdateCode,
	requestUpdateCodeSub,
	requestRemoveCodeSuper,
	requestRemoveCode,
	requestRemoveCodeSub,
	requestCode,
	requestAllCode
} from '@/apis/code_api'

const state = {
	resultAllCodeList: [],
	codeDetail: {},
	codeList: [],
	codePage: null,
	codePageTotal: 0,
	codeTotalElemtnts: 0,
	
	setting:{

	},
	religionList: [],

	requestAllFlag : false
}

const getters = {
	getAllCodeList(state) {
		return state.resultAllCodeList
	},
	getCodeDetail(state) {
		return state.codeDetail
	},
	getCodePage(state) {
		if (state.codePage === null) {
			return 0
		} else {
			return state.codePage.pageNumber + 1;
		}
	},
	getCodePageTotal(state) {
		return state.codePageTotal;
	},
	getCodeTotalElements(state) {
		return state.codeTotalElemtnts;
	},
	getSetting(state) {
		return state.setting
	},
	getReligionList(state) {
		return state.religionList
	},
	getRequestAllFlag(state) {
		return state.requestAllFlag
	},
}

const mutations = {
	CODE_LIST(state, data) {
		state.codeList = data.resultCodeList
	},
	CODE_ALL_LIST(state, data) {
		state.resultAllCodeList = data.resultAllCodeList
	},
	SETTING_ITEM(state, data) {
		state.setting = data.resultSetting
	},
	RELIGION_LIST(state, data) {
		state.religionList = data.resultReligionList
	},
	REQUEST_ALL_FINISH(state){
		state.requestAllFlag = true
	}
}

const actions = {
	async REQUEST_CODE_LIST(context, parameter) {
		try {
			context.commit('START_LOADING')
			const response = await requestCode(parameter)
			if (response.data !== null && response.data != undefined && response.data.result === 'success') {
				context.commit('CODE_LIST', response.data)
			} else {
				alert(response.data.message)
			}
			context.commit('END_LOADING')
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING')
			alert('네트워크 장애로 인해 서버와 연결이 좋지 않아 정상적인 수행에 실패하였습니다.')
		}
		context.commit('END_LOADING');
	},
	async REQUEST_ALL_CODE_LIST(context) {
		try {
			context.commit('START_LOADING')
			const response = await requestAllCode()
			if (response.data !== null && response.data != undefined && response.data.result === 'success') {
				context.commit('CODE_ALL_LIST', response.data)
				context.commit("SETTING_ITEM", response.data);
				context.commit("RELIGION_LIST",response.data);
				context.commit("REQUEST_ALL_FINISH");
			} else {
				alert(response.data.message)
			}
			context.commit('END_LOADING')
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING')
			alert('네트워크 장애로 인해 서버와 연결이 좋지 않아 정상적인 수행에 실패하였습니다.')
		}
		context.commit('END_LOADING');
	}
}

export default { mutations, state, actions, getters }