import axios from 'axios'

function requestBoardList(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/board/board-page`, parameter.board)
}
function requestBoardDetail(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/board/board-detail?id=${id}`)
}

export {
	requestBoardList,
	requestBoardDetail
}
