import Vue from "vue";
import VueRouter from "vue-router";
import mainRouter from "./main/router";
import loginRouter from "./login/router";

import obituaryRouter from "./obituary/router";
import noticeRouter from "./notice/router";
import faqRouter from "./faq/router";
import formatRouter from "./format/router";
import displayRouter from "./display/router";
import shopRouter from "./shop/router";
import mortuaryRouter from "./mortuary/router";
import tabletRouter from "./tablet/router";
import boardRouter from "./board/router";
import corpRouter from "./corp/router";
import movieRouter from "./movie/router";
import paybackRouter from "./payback/router";

Vue.use(VueRouter);

const routes = [
  ...mainRouter,
  ...loginRouter,
  ...obituaryRouter,
  ...noticeRouter,
  ...faqRouter,
  ...formatRouter,
  ...displayRouter,
  ...shopRouter,
  ...mortuaryRouter,
  ...tabletRouter,
  ...boardRouter,
  ...corpRouter,
  ...movieRouter,
  ...paybackRouter
];

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
  }
});

export default router;
