import axios from 'axios'
import store from '@/store'
import router from '@/router'

import { setTokenInLocalStorage } from '@/utils/oauth'

/*
    모든 요청 전 header에 access_token을 담아 전송한다.
 */
axios.interceptors.request.use(
	config => {
		let accessToken = localStorage.getItem('access_token')
		if (accessToken != null && accessToken != undefined && accessToken != '' && (config.headers.Authorization === null || config.headers.Authorization === undefined)  && config.url.indexOf('code-AllList')===-1) {
			config.headers.Authorization = accessToken
		}
		if (config.data != null && config.data != undefined && config.data != undefined && config.data != ''
			&& config.data.page != null && config.data.page != undefined && config.data != '') {
			const pageSet = config.url.indexOf('?')>-1?'&page=':'?page=';
			config.url = config.url + pageSet + config.data.page.page + '&size=' + config.data.page.size + '&sort=' + config.data.page.sort;
		}
		return config
	},
	error => {
		return Promise.reject(error)
	}
)

/*
    만료된 access_token으로 요청시 Access token exprited가 발생하면 refresh 토큰으로 새로운 토큰을 받는다.
 */
axios.interceptors.response.use(
	response => {
		if (response.data === 1) {
			attemptRefreshToken()
		}
		if (response.data === 2) {
			console.log(response.data)
		}
		return response
	},
	function (error) {
		if (!error.response) {
			router.push('/error')
		}
		let status = error.response.status
		if (status === 500) {
			store.commit('PUSH_ERROR_PAGE')
			return Promise.reject(error)
		}
		if (status === 401) {
			let errorData = error.response.data
			if (errorData.error != 'invalid_token') {
				return Promise.reject(error)
			}
			if (isExpiredToken(errorData)) {
				attemptRefreshToken()
				return Promise.reject('refresh')
			} else {
				store.commit('LOGOUT_WITH_TOKEN_INVALIDE')
			}
		}
		return Promise.reject(error)
	}
)

function requestRefreshToken() {
	let form = new FormData()
	form.append('grant_type', 'refresh_token')
	form.append('refresh_token', localStorage.getItem('refresh_token'))
	const requestData = {
		url: `${process.env.VUE_APP_BASEURL}/oauth/token`,
		method: 'POST',
		auth: {
			username: process.env.VUE_APP_CLIENTID,
			password: process.env.VUE_APP_CLIENTSECRET
		},
		data: form
	}
	return axios(requestData)
}

function isExpiredToken(errorData) {
	let errorDescription = errorData.error_description
	return errorDescription.substring(0, 20) === 'Access token expired'
}

function attemptRefreshToken() {
	//store.commit('SET_SNACKBAR', setSnackBarInfo('토큰이 만료되어 재발급 합니다.', 'error', 'top'))
	return requestRefreshToken()
		.then(res => {
			setTokenInLocalStorage(res.data)
			location.reload();
			//store.commit('SET_SNACKBAR', setSnackBarInfo('토큰이 재발급 되었습니다.', 'info', 'top'))
		})
		.catch(() => {
			//store.commit('SET_SNACKBAR', setSnackBarInfo('Refresh Token이 만료되었습니다. 다시 한번 로그인해주세요.', 'error', 'top'))
			store.commit('LOGOUT_WITH_TOKEN_INVALIDE')
		})
}

const deleteAccessTokenInHeader = () => {
	axios.defaults.headers.common.Authorization = null
}

function requestMain(){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/main/main`)
}

function requestEncode(parameter){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/common/encoding?str=${parameter}`)
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/common/encoding?str=${parameter}`)
}

export { deleteAccessTokenInHeader,requestMain,requestEncode }
