import {
	requestNoticeList,
	requestNoticeDetail
} from "@/apis/notice_api";

const state = {
	noticeList: [],
	noticePage: null,
	noticePageTotal: 0,
	noticeTotalElemtnts: 0,
	noticeDetail: {
		noticeTitle: '',
		noticeContent: '',
		noticeFile: [],
		regDate: new Date()
	}
};

const getters = {
	getNoticeList(state) {
		return state.noticeList;
	},
	getNoticePage(state) {
		if (state.noticePage === null) {
			return 0;
		} else {
			return state.noticePage.pageNumber + 1;
		}
	},
	getNoticePageTotal(state) {
		return state.noticePageTotal;
	},
	getNoticeTotalElements(state) {
		return state.noticeTotalElemtnts;
	},
	getNoticeDetail(state) {
		return state.noticeDetail
	}
};

const mutations = {
	NOTICE_LIST(state, data) {
		state.noticeList = data.resultNoticeList
		state.noticePage = data.page
		state.noticePageTotal = data.pageTotal
		state.noticeTotalElemtnts = data.totalElemtnts
	},
	NOTICE_LIST_ADD(state, data) {
		state.noticeList = [].concat.apply(state.noticeList, data.resultNoticeList);
		state.noticePage = data.page
		state.noticePageTotal = data.pageTotal
		state.noticeTotalElemtnts = data.totalElemtnts
	},
	NOTICE_DETAIL(state, data) {
		state.noticeDetail = data.resultNotice
	},
	NOTICE_INIT(state) {
		state.noticeList = []
		state.noticePage = null
		state.noticePageTotal = 0
		state.noticeTotalElemtnts = 0
		state.noticeDetail = {
			noticeTitle: '',
			noticeContent: '',
			noticeFile: [],
			regDate: new Date()
		}
	}
};

const actions = {
	async REQUEST_NOTICE_LIST(context, parameter) {
		try {
			context.commit('START_LOADING');
			const response = await requestNoticeList(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("NOTICE_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async REQUEST_NOTICE_DETAIL(context, parameter) {
		try {
			context.commit('START_LOADING');
			const response = await requestNoticeDetail(parameter.id);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("NOTICE_DETAIL", response.data);
				context.commit('END_LOADING');
				return true;
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
			return false;
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
			return false;
		}
	}
};

export default { mutations, state, actions, getters };