import axios from 'axios'

function requestTabletList(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/tablet/tablet-page`, parameter.tablet)
}

function registTablet(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/tablet/regist/tablet`,parameter.tablet)
}

function registTablet2(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/tablet/regist/tablet2`,parameter.tablet)
}
function registTablet3(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/tablet/regist/tablet3`,parameter.tablet)
}

function requestTabletDelete(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/tablet/delete/tablet?id=${encodeURIComponent(id)}`)

}
export {
	requestTabletList,
		registTablet,
		requestTabletDelete,
		registTablet2,
		registTablet3
}
