import axios from 'axios'

function requestShopList(parameter, pageable) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/shop/shop-page?shopType=${parameter.shopType}&EcodeId=${parameter.EcodeId}`, {
		...parameter.shop,
		page: pageable
	})
}

function requestShopType(){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/shop/shop-type`)
}


function requestShopDetail(parameter){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/shop/shop-detail?shopId=${parameter.id}&EcodeId=${parameter.EcodeId}`)
}

function requestObituaryShopInfo(encodedId){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/shop/shop-obituary-info?EcodeId=${encodedId}`)

}
export {
	requestShopList,
	requestShopType,
	requestShopDetail,
	requestObituaryShopInfo
}
