import {
	requestDisplay,
	requestDisplayList,
	requestRegistMortuaryDisplayState
} from "@/apis/display_api";

import router from "@/router";

const state = {
	displayMortuaryList:[],
	displayEnterRoomList:[],
	displayList:[]

};

const getters = {
	getDisplayList(state){
		return state.displayList
	},	
	getDisplayEnterRoomList(state){
		return state.displayEnterRoomList
	},
	getDisplayMortuaryList(state){
		return state.displayMortuaryList
	},
};


const mutations = {
	DISPLAY_MORTUARY_LIST(state,data){
		state.displayMortuaryList = data.resultMortuary
	},
	DISPLAY_ENTER_ROOM_LIST(state,data){
		state.displayEnterRoomList = data.resultEnterRoom
	},
	DISPLAY_LIST(state,data){
		state.displayList = data.resultDisplay
	}
};

const actions = {
	async REQUEST_DISPLAY(context,parameter) {
		try {
			context.commit('START_LOADING');
            const response = await requestDisplay(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				router.push('/')

			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async REQUEST_DISPLAY_LIST(context,parameter) {
		try {
			context.commit('START_LOADING');
            const response = await requestDisplayList(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit('DISPLAY_MORTUARY_LIST',response.data)
				context.commit('DISPLAY_ENTER_ROOM_LIST',response.data)
				context.commit('DISPLAY_LIST',response.data)

			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async REQUEST_REGIST_MORTUARY_DISPLAYSTATE(context,parameter){
		try {
			context.commit('START_LOADING');
            const response = await requestRegistMortuaryDisplayState(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit('DISPLAY_MORTUARY_LIST',response.data)
				context.commit('DISPLAY_LIST',response.data)

			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	}
};

export default { mutations, state, actions, getters };