import {
	requestSurNameList,
} from "@/apis/surNameSearch_api";

const state = {
	surNameList:[],
};

const getters = {
	getSurNameList(state) {
		return state.surNameList;
	}
};

const mutations = {
	SUR_NAME_LIST(state, data){
    state.surNameList = data.resultSurNameList
	},
	SUR_NAME_INIT(state){
		state.surNameList = []
	}
};

const actions = {
	async REQUEST_SUR_NAME_LIST(context, parameter) {
		try {
			context.commit('START_LOADING');
      const response = await requestSurNameList(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
					context.commit("SUR_NAME_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
	}
};

export default { mutations, state, actions, getters };