import {
	requestRegistMusic,
	requestDeleteMusic
} from "@/apis/music_api";
import router from "@/router";

const state = {
    musicList:[],
};

const getters = {
	getMusicList(state) {
		return state.musicList;
	},

};


const mutations = {
	MUSIC_LIST(state, data){
        state.musicList = data.resultMusic
	},
	INIT_MUSIC(state){
		state.musicList= []

	}
};

const actions = {
	
	async REQUEST_REGIST_MUSIC(context,parameter){
		try {
			context.commit('START_LOADING');
            const response = await requestRegistMusic(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("MUSIC_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
			context.commit("OPEN_MODAL", {
				title: "회원가입에 실패",
				content: e.response.data.message,
				option1: "닫기"
			});
			alert('네트워크 장애로 인해 서버와 연결이 좋지 않아 정상적인 수행에 실패하였습니다.');
		}
	},
	async REQUEST_DELETE_MUSIC(context,parameter){
		try {
			context.commit('START_LOADING');
            const response = await requestDeleteMusic(parameter.id);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("MUSIC_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
			context.commit("OPEN_MODAL", {
				title: "회원가입에 실패",
				content: e.response.data.message,
				option1: "닫기"
			});
			alert('네트워크 장애로 인해 서버와 연결이 좋지 않아 정상적인 수행에 실패하였습니다.');
		}
	},
};

export default { mutations, state, actions, getters };