const setTokenInLocalStorage = tokenInfo => {
  localStorage.setItem("access_token", "Bearer " + tokenInfo.access_token);
  localStorage.setItem("refresh_token", tokenInfo.refresh_token);
};

// 2020-05-22, DB 설계 내용과 동일하게 field명 변경
const deleteTokenInLocalStorage = () => {
  localStorage.removeItem("access_token");
  localStorage.removeItem("refresh_token");
  localStorage.removeItem("memberId");
  localStorage.removeItem("memberName");
  localStorage.removeItem("memberAddr");
  localStorage.removeItem("memberClass");
  localStorage.removeItem("memberAttr");
  localStorage.removeItem("memberAddr");
  localStorage.removeItem("memberAddrX");
  localStorage.removeItem("memberAddrY");
  localStorage.removeItem("memberPhone");
  localStorage.removeItem("memberPlaceName");
  localStorage.removeItem("memberCorpCode");
  localStorage.removeItem("memberCorpName");
};
// 2020-05-22, DB 설계 내용과 동일하게 field명 변경
const setMemberInfoLocalStorage = member => {
  localStorage.setItem("memberId", member.id);
  localStorage.setItem("memberName", member.memberName);
  localStorage.setItem("memberAddr", member.memberAddr);
  localStorage.setItem("memberClass", member.memberClass);
  localStorage.setItem("memberAttr", member.memberAttr.codeId);
  localStorage.setItem("memberAddr", member.memberAddr);
  localStorage.setItem("memberAddrX", member.memberAddrX);
  localStorage.setItem("memberAddrY", member.memberAddrY);
  localStorage.setItem("memberPhone", member.memberPhone);
  localStorage.setItem("memberPlaceName", member.memberPlaceName);
  if(member.memberCorpCode!==null && member.memberCorpCode!==undefined){
    localStorage.setItem("memberCorpCode", member.memberCorpCode.codeId);
    localStorage.setItem("memberCorpName", member.memberCorpCode.codeName);

  }
  localStorage.setItem("memberCorpMaster", member.memberCorpMaster);
};
export {
  deleteTokenInLocalStorage,
  setTokenInLocalStorage,
  setMemberInfoLocalStorage
};
