import axios from 'axios'


function requestMortuaryList() {
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/mortuary/list`)
}

function requestMortuaryAndEnterList(){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/mortuary/EnterAndMortuaryList`)
}



function requestMortuaryRegist(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/mortuary/regist/mortuary`,parameter)
}

function requestEnterRoomRegist(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/mortuary/regist/enterRoom`,parameter)
}
export {
	requestMortuaryList,
	requestMortuaryAndEnterList,
	requestEnterRoomRegist,
	requestMortuaryRegist
}
