import {
	requestAddressSearch,
	requestFuneralAddressSearch,
	requestAddressSearchPostCode
} from "@/apis/addressSearch_api";


const state = {
	addressSearchList:[],
};

const getters = {
	getAddressSearchList(state) {
		return state.addressSearchList;
	},
};


const mutations = {
	ADDRESS_SEARCH_LIST(state, data){
		if( data.documents === null ||  data.documents===undefined){

			state.addressSearchList = data.resultFuneralAddr
		}else{

			state.addressSearchList = data.documents
		}
	},
};

const actions = {
	async REQUEST_ADDRESS_SEARCH_LIST(context,parameter) {
		try {
			context.commit('START_LOADING');
            const response = await requestAddressSearch(parameter);
			if (response.data !== null && response.data !== undefined && (response.data.errorType === null ||  response.data.errorType === undefined)) {
				context.commit("ADDRESS_SEARCH_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	
	async REQUEST_ADDRESS_SEARCH_FUNERAL_LIST(context,parameter) {
		try {
			context.commit('START_LOADING');
            const response = await requestFuneralAddressSearch(parameter);
			if (response.data !== null && response.data !== undefined && (response.data.errorType === null ||  response.data.errorType === undefined)) {
				context.commit("ADDRESS_SEARCH_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
	},
	async REQUEST_ADDRESS_SEARCH_POST_CODE(context,parameter) {
		const param = {}
		try {
			
			context.commit('START_LOADING');
            const response = await requestAddressSearchPostCode(parameter);
			if (response.data !== null && response.data !== undefined && response.data.documents!==null && response.data.documents!==undefined && response.data.documents.length>0) {
				if(response.data.documents[0].road_address!==null && response.data.documents[0].road_address!== undefined){

					param.post = response.data.documents[0].road_address.zone_no;
				}else{

					param.post = response.data.documents[0].address.zip_code;
				}
				param.result = true

			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		return param
	},
};

export default { mutations, state, actions, getters };