import {
	requestShopList,
	requestShopType,
	requestShopDetail,
	requestObituaryShopInfo
} from "@/apis/shop_api";

const state = {
	shopDetail: {
		id: null,
		shopTitle: '',
		shopTxt: '',
		shopPrice: 0,
		shopDc: 0,
		shopType: {
			codeSubId: null
		},
		shopSavings: 0,
		shopOpen: '',
		shopSell: '',
		shopInventory: '',
		shopInventoryCount: 0,
		shopDelivery: {
			id: null
		},
		shopFile: [],
		shopQna: [],
		shopShopClass: [],
		deliveryCost:0
	},
	shopObituaryDetail: {},
	shopList: [],
	shopTypeList: [],
	shopUse:'none'
};

const getters = {
	getShopDetail(state) {
		return state.shopDetail
	},
	getShopObituaryDetail(state) {
		return state.shopObituaryDetail
	},
	getShopList(state) {
		return state.shopList
	},
	getShopTypeList(state) {
		return state.shopTypeList
	},
	getShopUse(state) {
		return state.shopUse
	},
};

const mutations = {
	SHOP_LIST(state, data) {
		state.shopList = data.resultShopList
		state.shopUse = data.shopUse
	},
	SHOP_LIST_ADD(state, data) {
		state.shopList = [].concat.apply(state.shopList, data.resultShopList);
		state.shopUse = data.shopUse
	},
	SHOP_DETAIL(state, data) {
		state.shopDetail = data.resultShop
	},
	SHOP_TYPE(state, data) {
		state.shopTypeList = data.resultShopClassCodeList
	},
	INIT_SHOP_LIST(state) {

		state.shopUse = 'none'
		state.shopList = []
	},
	SHOP_OBITUARY_DETAIL(state, data) {
		state.shopObituaryDetail = data.obituaryDetail
	},
	INIT_SHOP_DETAIL(state) {
		state.shopDetail = {
			id: null,
			shopTitle: '',
			shopTxt: '',
			shopPrice: 0,
			shopDc: 0,
			shopType: {
				codeSubId: null
			},
			shopSavings: 0,
			shopOpen: '',
			shopSell: '',
			shopInventory: '',
			shopInventoryCount: 0,
			shopDelivery: {
				id: null
			},
			shopFile: [],
			shopQna: [],
			shopShopClass: [],
			deliveryCost:0
		},
			state.obituaryDetail = {}
	}
};

const actions = {
	async REQUEST_SHOP_LIST(context, parameter) {
		try {
			context.commit('START_LOADING')
			// 페이징 처리 여부 확인
			if (!parameter.page) {
				context.commit("PAGE_INIT");
			} else {
				if (context.getters.getTotalPage - 1 <= context.getters.getCurrentPage) {
					return
				}
				context.commit("PAGE_NEXT");
			}
			// 페이지 정보 전달
			const response = await requestShopList(parameter, context.getters.getPageable)
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				// 총 페이지 수 전달
				context.commit("PAGE_TOTAL", response.data.pageTotal)
				// 페이징 처리 여부 확인
				if (!parameter.page) {
					context.commit("SHOP_LIST", response.data)
				} else {
					context.commit("SHOP_LIST_ADD", response.data);
				}
			} else {
				alert(response.data.message)
			}
			context.commit('END_LOADING')
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING')
			alert('Shop 조회에 실패하였습니다.')
		}
		context.commit('END_LOADING');
	},
	async REQUEST_SHOP_TYPE(context,parameter) {
		try {
			context.commit('START_LOADING')
			const response = await requestShopType()
			
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("SHOP_TYPE", response.data)
				if(response.data.resultShopClassCodeList!== null && response.data.resultShopClassCodeList!==undefined && response.data.resultShopClassCodeList.length>0){

					parameter.context.changeType(response.data.resultShopClassCodeList[0])
				}
			} else {
				alert(response.data.message)
			}
			context.commit('END_LOADING')
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING')
			alert('Shop 타입 조회에 실패하였습니다.')
		}
		context.commit('END_LOADING');
	},
	async REQUEST_SHOP_DETAIL(context, parameter) {
		try {
			context.commit("START_LOADING");
			const response = await requestShopDetail(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("SHOP_DETAIL", response.data);
			} else {
				alert(response.data.message)
			}
			context.commit("END_LOADING");
		} catch (e) { if(e==='refresh'){ return }
			context.commit("END_LOADING");
			alert('Shop 조회에 실패하였습니다.')
		}
		context.commit('END_LOADING');
	},
	async REQUEST_OBITUARY_SHOP_INFO(context, parameter) {
		try {
			context.commit("START_LOADING");
			const response = await requestObituaryShopInfo(parameter.EcodeId);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("SHOP_OBITUARY_DETAIL", response.data);
			} else {
				alert(response.data.message)
			}
			context.commit("END_LOADING");
		} catch (e) { if(e==='refresh'){ return }
			context.commit("END_LOADING");
			alert('Shop 조회에 실패하였습니다.')
		}
		context.commit('END_LOADING');
	}
};

export default { mutations, state, actions, getters };