import axios from 'axios'

function requestNoticeList(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/notice/notice-page`, parameter.notice)
}
function requestNoticeDetail(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/notice/notice-detail?id=${id}`)
}

export {
	requestNoticeList,
	requestNoticeDetail
}
