import {
	requestMortuaryList,
	requestMortuaryAndEnterList,
	requestMortuaryRegist,
	requestEnterRoomRegist
} from "@/apis/mortuary_api";

const state = {
	mortuaryList: [],
	enterRoomList: []
};

const getters = {
	getMortuaryList(state) {
		return state.mortuaryList;
	},
	getEnterRoomList(state) {
		return state.enterRoomList;
	},
};

const mutations = {
	MORTUARY_LIST(state, data) {
		if( data.resultMortuary.length===0){

			state.mortuaryList = [{ id: null, mortuaryNumber: null }]
		}else{

			state.mortuaryList = data.resultMortuary
		}
	},
	MORTUARY_ADD_LIST(state) {
		state.mortuaryList.push({ id: null, mortuaryNumber: null })
	},
	MORTUARY_REMOVE_LIST(state, data) {
		state.mortuaryList.splice(data.index, 1)
	},
	ENTER_ROOM_REMOVE_LIST(state, data) {
		state.enterRoomList.splice(data.index, 1)
	},
	ENTER_ROOM_LIST(state, data) {
        state.enterRoomList = data.resultEnterRoom
	},
	ENTER_ROOM_ADD_LIST(state) {
		state.enterRoomList.push({ id: null, enterRoomNumber: null })
	},
	REQUEST_MORTUARY_AND_ENTER_LIST(state, data) {
		state.enterRoomList.splice(data.index, 1)
	}
};

const actions = {
	async REQUEST_OBITUARY(context) {
		try {
			context.commit('START_LOADING');
			const response = await requestMortuaryList();
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("MORTUARY_LIST", response.data);
				context.commit("GRAVE_LIST", response.data);
				context.commit("ENTER_ROOM_LIST", response.data);
				context.commit("MUSIC_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async REQUEST_MORTUARY_AND_ENTER_LIST(context) {
		try {
			context.commit('START_LOADING');
			const response = await requestMortuaryAndEnterList();
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("MORTUARY_LIST", response.data);
				context.commit("ENTER_ROOM_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async MORTUARY_REGIST(context, data) {
		try {
			context.commit('START_LOADING');
			const response = await requestMortuaryRegist(data.mortuary);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("MORTUARY_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async ENTER_ROOM_REGIST(context, data) {
		try {
			context.commit('START_LOADING');
			const response = await requestEnterRoomRegist(data.enterRoom);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("ENTER_ROOM_LIST", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	}
};

export default { mutations, state, actions, getters };