import axios from 'axios'

function requestJoinMember(parameter) {
	let form = new FormData();
	for (let i = 0; i < parameter.files.length; i++) {
		form.append("fileList", parameter.files[i]);
	}
	const memberParam = parameter.member
	var memberParamString = JSON.stringify(memberParam);
	form.append("memberParamString", memberParamString)
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/members/join`, form,
		{
			headers: {
				'Content-Type': 'multipart/form-data'
			}
		});
}

function requestLogin(member) {
	const form = new FormData()
	form.append('username', member.id)
	form.append('password', member.memberPw)
	form.append('grant_type', 'password')
	const requestData = {
		url: `${process.env.VUE_APP_BASEURL}/oauth/token`,
		method: 'POST',
		auth: {
			username: process.env.VUE_APP_CLIENTID,
			password: process.env.VUE_APP_CLIENTSECRET
		},
		data: form
	}
	return axios(requestData)
}

function requestMemberDetail() {
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/admin/members/member-detail`)
}

function requestMemberClickDetail(memberId) {
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/admin/members/member-click-detail?id=${memberId}`)
}

