import axios from 'axios'

function requestRegistCodeSuper(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/regist/codeSuper`, requestBody)
}

function requestRegistCode(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/regist/code`, requestBody)
}

function requestRegistCodeSub(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/regist/codeSub`, requestBody)
}

function requestUpdateCodeSuper(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/update/codeSuper`, requestBody)
}

function requestUpdateCode(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/update/code`, requestBody)
}

function requestUpdateCodeSub(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/update/codeSub`, requestBody)
}

function requestRemoveCodeSuper(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/remove/codeSuper`, requestBody)
}

function requestRemoveCode(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/remove/code`, requestBody)
}

function requestRemoveCodeSub(requestBody) {
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/admin/code/remove/codeSub`, requestBody)
}

function requestCode(val) {
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/admin/code/code-list?codeSuperId=${val}`)
}

function requestAllCode() {
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/admin/code/code-AllList`)
}

export {
	requestRegistCodeSuper,
	requestRegistCode,
	requestRegistCodeSub,
	requestUpdateCodeSuper,
	requestUpdateCode,
	requestUpdateCodeSub,
	requestRemoveCodeSuper,
	requestRemoveCode,
	requestRemoveCodeSub,
	requestCode,
	requestAllCode
}
