const state = {
	formatTypeList: [],
	formatTypeRightFlag: [],
	formList: [],
	formListRightFlag: []
};

const getters = {
	getFormatTypeList(state) {
		return state.formatTypeList;
	},
	getFormatTypeRightFlag(state) {
		return state.formatTypeRightFlag;
	},
	getFormatList(state) {
		return state.formList
	},
	getFormListRightFlag(state) {
		return state.formListRightFlag;
	}
};

const mutations = {
	FORMAT_TYPE_LIST(state, data) {
		let formatTypeRightFlagCopy = []
		for (let i = 0; i < data.resultFormatTypeList.length; i++) {
			formatTypeRightFlagCopy.push(false)
		}
		state.formatTypeRightFlag = formatTypeRightFlagCopy;
		state.formatTypeList = data.resultFormatTypeList;
	},
	FORMAT_LIST(state, data) {
		let formListRightFlagCopy = []
		for (let i = 0; i < data.resultFormatList.length; i++) {
			formListRightFlagCopy.push(false)
		}
		state.formListRightFlag = formListRightFlagCopy
		state.formList = data.resultFormatList
	},
	FORMAT_INIT(state) {
		state.formatTypeList = []
		state.formatTypeRightFlag = []
		state.formList = []
		state.formListRightFlag = []
	},
	FORMAT_TYPE_RIGHT_CLICK(state, index) {
		let formatTypeRightFlagCopy = JSON.parse(JSON.stringify(state.formatTypeRightFlag))
		for (let i = 0; i < formatTypeRightFlagCopy.length; i++) {
			formatTypeRightFlagCopy[i] = false
		}
		formatTypeRightFlagCopy[index] = true
		state.formatTypeRightFlag = formatTypeRightFlagCopy;
	},
	FORMAT_TYPE_RIGHT_CLICK_INIT(state) {
		let formatTypeRightFlagCopy = JSON.parse(JSON.stringify(state.formatTypeRightFlag))
		for (let i = 0; i < formatTypeRightFlagCopy.length; i++) {
			formatTypeRightFlagCopy[i] = false
		}
		state.formatTypeRightFlag = formatTypeRightFlagCopy;
	},
	FORMAT_RIGHT_CLICK(state, index) {
		let formListRightFlagCopy = JSON.parse(JSON.stringify(state.formListRightFlag))
		for (let i = 0; i < formListRightFlagCopy.length; i++) {
			formListRightFlagCopy[i] = false
		}
		formListRightFlagCopy[index] = true
		state.formListRightFlag = formListRightFlagCopy;
	},
	FORMAT_RIGHT_CLICK_INIT(state) {
		let formListRightFlagCopy = JSON.parse(JSON.stringify(state.formListRightFlag))
		for (let i = 0; i < formListRightFlagCopy.length; i++) {
			formListRightFlagCopy[i] = false
		}
		state.formListRightFlag = formListRightFlagCopy;
	}
};

