import {
	requestFaqList,
} from "@/apis/faq_api";

const state = {
	faqList: [],
	faqPage: null,
	faqPageTotal: 0,
	faqTotalElemtnts: 0,

};

const getters = {
	getFaqList(state) {
		return state.faqList;
	},
	getFaqPage(state) {
		if (state.faqPage === null) {
			return 0;
		} else {
			return state.faqPage.pageNumber + 1;
		}
	},
	getFaqPageTotal(state) {
		return state.faqPageTotal;
	},
	getFaqTotalElements(state) {
		return state.faqTotalElemtnts;
	}
};

const mutations = {
	FAQ_LIST(state, data) {
		state.faqList = data.resultFaqList
		state.faqPage = data.page
		state.faqPageTotal = data.pageTotal
		state.faqTotalElemtnts = data.totalElemtnts
	},
	FAQ_LIST_ADD(state, data) {
		state.faqList = [].concat.apply(state.faqList, data.resultFaqList);
		state.faqPage = data.page
		state.faqPageTotal = data.pageTotal
		state.faqTotalElemtnts = data.totalElemtnts
	},
	FAQ_INIT(state) {
		state.faqList = []
		state.faqPage = null
		state.faqPageTotal = 0
		state.faqTotalElemtnts = 0
	}
};

const actions = {
	async REQUEST_FAQ_LIST(context, parameter) {
		try {
			context.commit('START_LOADING');
			const response = await requestFaqList(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				if (response.data.page.pageNumber !== 0) {
					context.commit("FAQ_LIST_ADD", response.data);
				} else {
					context.commit("FAQ_LIST", response.data);
				}
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
};

export default { mutations, state, actions, getters };