import axios from 'axios'

function requestDisplay(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/requestDisplay/reg`, parameter.requestDisplay)
}

function requestDisplayList(){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/requestDisplay/list`)

}

function requestRegistMortuaryDisplayState(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/requestDisplay/DisplayStateReg`, parameter.displayState)
}
export {
	requestDisplay,
	requestDisplayList,
	requestRegistMortuaryDisplayState
}
