// constant
export const LOAD_MORTICIAIN = "loadMorticiain";

import router from "@/router";

const state = {
  period: `${new Date().getFullYear()}년 ${new Date().getMonth() +
    1}월 ~ ${new Date().getFullYear()}년 ${new Date().getMonth() + 1}월`,
  years: [`${new Date().getFullYear() - 1}`, `${new Date().getFullYear()}`],
  months: [],
  selectStartYear: new Date().getFullYear(),
  selectStartMonth: new Date().getMonth() + 1,
  selectEndYear: new Date().getFullYear(),
  selectEndMonth: new Date().getMonth() + 1,
  step: "phone-certify",
  bank: "",
  bankCertifyStep: "select-bank",
  name: "",
  phone: ""
};

const getters = {
  getYears: state => state.years,
  getMonths: state => state.months,
  getStartYear: state => state.selectStartYear,
  getStartMonth: state => state.selectStartMonth,
  getEndYear: state => state.selectEndYear,
  getEndMonth: state => state.selectEndMonth,
  getPeriod: state => state.period
};

const mutations = {
  setYears(state, data) {
    state.years = data;
  },
  setMonths(state, data) {
    state.months = data;
  },
  setStartYear(state, data) {
    state.selectStartYear = data;
  },
  setStartMonth(state, data) {
    state.selectStartMonth = data;
  },
  setEndYear(state, data) {
    state.selectEndYear = data;
  },
  setEndtMonth(state, data) {
    state.selectEndMonth = data;
  },
  setPeriod(state, data) {
    state.period = data;
  },
  setNamePhone(state, data) {
    const { name, phone } = data;
    state.name = name;
    state.phone = phone;
  },
  setVerifyStatus(state, data) {
    state.step = data;
  },
  setBankInfo(state, data) {
    state.bank = data;
    state.step = "bank-certify";
    state.bankCertifyStep = "input-account";
  },
  setCertifyStep(state, data) {
    state.bankCertifyStep = data;
  },
  resetBankInfo(state, data) {
    state.bank = "";
    state.step = "";
  }
};

const actions = {
  async certifyPhone({ commit }, payload) {
    try {
      const { name, phone } = payload;
      commit("setNamePhone", { name, phone });

      const res = await this.$axios.post(
        "/mortician/account",
        {
          name,
          phone
        },
        {
          withCredentials: true
        }
      );

      if (res.status === 201) {
        alert(res.data);
        commit("setVerifyStatus", "phone-certify");
        return;
      }

      commit("setVerifyStatus", "send");
    } catch (err) {
      console.error(err);
    }
  },

  async certifyVerificationCode({ commit }, payload) {
    try {
      const { code } = payload;
      const res = await this.$axios.post(
        "/mortician/verification",
        {
          code
        },
        {
          withCredentials: true
        }
      );
      if (res.status === 203) {
        return alert(res.data);
      }
      alert("인증되었습니다");
      commit("setVerifyStatus", "bank-certify");
    } catch (err) {
      console.error(err);
    }
  },
  async accountCertify({ commit }, payload) {
    try {
      const { name, code, account } = payload;

      const res = await this.$axios.put(
        "/mortician/account",
        {
          name,
          bankCode: code,
          bankAccount: account
        },
        {
          withCredentials: true
        }
      );

      if (res.status === 203) {
        return alert(res.data);
      }
      const { bank_holder } = res.data.response;

      const confirmAccount = confirm(`예금주 ${bank_holder} 맞습니까?`);

      if (confirmAccount) {
        const res = await this.$axios.post(
          "/mortician/account/confirm",
          {
            name,
            bank_holder,
            bankAccount: account
          },
          {
            withCredentials: true
          }
        );
        if (res.status === 203) {
          return alert(res.data);
        }
        alert(res.data);
        commit("setVerifyStatus", "complete");
      } else {
        alert("취소되었습니다");
      }
    } catch (err) {
      console.error(err);
    }
  },
  async retryPhoneVerify({ commit }, payload) {
    try {
      const { morticianId } = payload;
      const res = await this.$axios.put(
        "/mortician/verification-reset",
        {
          morticianId
        },
        {
          withCredentials: true
        }
      );

      if (res.data) commit("setVerifyStatus", "retry");
    } catch (err) {
      console.error(err);
    }
  }
};

export default { mutations, state, actions, getters };

// store
