import axios from 'axios'



function requestObituaryList(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/obituary/list?endFlag=${encodeURIComponent(parameter.endFlag)}&searchTxt=${encodeURIComponent(parameter.searchTxt)}&searchType=${encodeURIComponent(parameter.searchType)}`, parameter.obituary)
}

function requestObituaryOnlySendMobile(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/obituary/sendMobile?id=${id}`)
}


function requestObituaryAllSendMobile(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/obituary/sendAllMobile?id=${id}`)
}

function requestObituaryOnlySendMovieMobile(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/obituary/sendMovieMobile?id=${id}`)

}

function requestObituaryDelete(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/obituary/delete?endFlag=${encodeURIComponent(parameter.endFlag)}&searchTxt=${encodeURIComponent(parameter.searchTxt)}&searchType=${encodeURIComponent(parameter.searchType)}`, parameter.obituary)
}


function requestObituaryReg(parameter){
	console.log('requestObituaryReg')
	let form = new FormData();
	for(let i=0;i<parameter.files.length;i++){
	  form.append("fileList", parameter.files[i]);
	}
	const obituaryParam = parameter.obituary
	var obituaryParamString = JSON.stringify(obituaryParam);
	form.append("obituaryParamString",obituaryParamString)
  
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/obituary/reg`,form,
	{headers:{
	  'Content-Type' : 'multipart/form-data'
	}});

}

function requestObituaryDetail(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/obituary/detail?id=${id}`)
}

function requestObituaryDetailForPost(id){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/obituary/detailForPost?id=${id}`)
}

function requestObituaryForMobileList(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/obituary/listForMobile?endFlag=${encodeURIComponent(parameter.endFlag)}&searchTxt=${encodeURIComponent(parameter.searchTxt)}&searchType=${encodeURIComponent(parameter.searchType)}`, parameter.obituary)

}

function requestObituaryTransSms(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/obituary/obituarySmsPush`, parameter)
	//return axios.post(`${process.env.VUE_APP_BASEURL}/api/pay/smsPayPage`, parameter)

}
function requestObituaryDoEndTime(parameter){
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/obituary/doEndTime?endFlag=${encodeURIComponent(parameter.endFlag)}&searchTxt=${encodeURIComponent(parameter.searchTxt)}&searchType=${encodeURIComponent(parameter.searchType)}`, parameter.obituary)
}


export {
	requestObituaryList,
	requestObituaryDelete,
	requestObituaryReg,
	requestObituaryDetail,
	requestObituaryDetailForPost,
	requestObituaryTransSms,
	requestObituaryForMobileList, //모바일 푸시 메세지 보내기 용 
	requestObituaryDoEndTime,
	requestObituaryOnlySendMobile,
	requestObituaryAllSendMobile,
	requestObituaryOnlySendMovieMobile
}
