import axios from 'axios'

function requestRegistMusic(parameter){

	let form = new FormData();
	for(let i=0;i<parameter.files.length;i++){
	  form.append("fileList", parameter.files[i]);
	}
	const musicParam = parameter.music
	var musicParamString = JSON.stringify(musicParam);
	form.append("musicParamString",musicParamString)
  
	return axios.post(`${process.env.VUE_APP_BASEURL}/api/app/music/regist/music`,form,
	{headers:{
	  'Content-Type' : 'multipart/form-data'
	}});

}
function requestDeleteMusic(parameter){
	return axios.get(`${process.env.VUE_APP_BASEURL}/api/app/music/delete/music?id=${encodeURIComponent(parameter)}`)
}
export {
	requestRegistMusic,
	requestDeleteMusic
}
