import router from '@/router'
import {
	requestMain,
	requestEncode
} from '@/apis/common_api'

const state = {
	language: 'ko',
	loadingState: false,
	deemdedState: false,
	spinnerLoading: false,
	modal: { open: false, title: '', content: '', option1: '', option2: '' },
	errorMessage: '잘못된 요청입니다.',
	// ============ Rules ============ //
	emailRules: [
		v => !!v || '이메일을 작성해주세요.',
		v => /.+@.+\..+/.test(v) || '이메일 형식으로 작성해주세요.',
		v => v.search(/\s/) === -1 || '공백을 제거해주세요!'
	],
	nameRules: [
		v => !!v || '이름을 작성해주세요.',
		v => v.search(/\s/) === -1 || '공백을 제거해주세요!'
	],
	passwordRules: [
		v => !!v || '비밀번호를 작성해주세요'
	],
	mainNoticeList: [],
	mainBannerAdminList: [],
	mainBannerAdminList1: [],
	mainBannerAdminList2: [],
	obituaryCount: 0,
	obituaryReadCount: 0,
	page: {
		page: 0,
		size: 10,
		sort: 'regDate,desc'
	},
	pageTotal: 0,
}

const getters = {
	languageInfo(state) {
		return state.language
	},
	getloadingState(state) {
		return state.loadingState
	},
	getMainNoticeList(state) {
		return state.mainNoticeList
	},
	getMainBannerAdminList(state) {
		return state.mainBannerAdminList
	},
	getMainBannerAdminList1(state) {
		return state.mainBannerAdminList1
	},
	getMainBannerAdminList2(state) {
		return state.mainBannerAdminList2
	},
	getObituaryCount(state) {
		return state.obituaryCount
	},
	getObituaryReadCount(state) {
		return state.obituaryReadCount
	},
	getDeemState(state) {
		return state.deemdedState
	},
	getTotalPage(state) {
		return state.pageTotal
	},
	getCurrentPage(state) {
		return state.page.page
	},
	getPageSize(state) {
		return state.page.size
	},
	getPageable(state) {
		return state.page
	},
	getTotalElemtnts(state){
		return state.totalElemtnts
	}
}

const mutations = {
	CLOSE_MODAL(state) {
		state.modal.open = false
	},
	OPEN_MODAL(state, modalTexts) {
		state.loadingState = false
		state.modal.title = modalTexts.title
		state.modal.content = modalTexts.content
		state.modal.option1 = modalTexts.option1
		state.modal.option2 = modalTexts.option2 ? modalTexts.option2 : null
		state.modal.open = true
	},
	START_LOADING(state) {
		state.loadingState = true
	},
	END_LOADING(state) {
		state.loadingState = false
	},
	START_DEEMED(state) {
		state.deemdedState = true
	},
	END_DEEMED(state) {
		state.deemdedState = false
	},
	PUSH_ERROR_PAGE(state) {
		state.errorMessage = '잘못된 요청입니다.'
		router.push('/error')
	},
	START_SPINNER(state) {
		state.spinnerLoading = true
	},
	END_SPINNER(state) {
		state.spinnerLoading = false
	},
	MAIN_INIT(state, data) {
		state.mainNoticeList = data.resultNoticeList
		state.mainBannerAdminList = data.resultBannerAdminList
		state.obituaryCount = data.obituaryCount;
		state.obituaryReadCount = data.obituaryReadCount;
	},
	PAGE_INIT(state) {
		state.page = {
			page: 0,
			size: 10,
			sort: 'regDate,desc'
		}
	},
	PAGE_NEXT(state) {
		state.page = {
			page: state.page.page + 1,
			size: 10,
			sort: 'regDate,desc'
		}
	},
	PAGE_TOTAL(state, pageTotal) {
		if (pageTotal === null || pageTotal === undefined) {
			state.pageTotal = 1
		} else {
			state.pageTotal = pageTotal
		}
	}
}

const actions = {
	async REQUEST_MAIN(context, parameter) {
		try {
			context.commit('START_LOADING');
			const response = await requestMain(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				context.commit("MAIN_INIT", response.data);
			} else {
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
	},
	async REQUEST_ENCODE(context, parameter) {
		const param = {}
		try {
			context.commit('START_LOADING');
			const response = await requestEncode(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				param.str = response.data.encodeStr
				param.result = true
			} else {
				param.result = false
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			context.commit('END_LOADING');
		}
		context.commit('END_LOADING');
		return param
	},
};

export default { mutations, state, actions, getters }
