import {
	requestAccountCertify,
} from "@/apis/account_api";

import router from "@/router";

const state = {
};

const getters = {

};


const mutations = {
	
};

const actions = {
	async REQUEST_ACCOUNT_CERTIFY(context,parameter) {
		let param = {}
		try {
			context.commit('START_LOADING');
            const response = await requestAccountCertify(parameter);
			if (response.data !== null && response.data !== undefined && response.data.result === 'success') {
				if(response.data.accountInfo.resultCode==='0000'){
					param.result =true
					param.name= response.data.accountInfo.accountName
				}else{
					alert(response.data.accountInfo.resultMessage)
					param.result =false
				}
			} else {
				param.result =false
				alert(response.data.message);
			}
			context.commit('END_LOADING');
		} catch (e) { if(e==='refresh'){ return }
			param.result =false
			context.commit('END_LOADING');
		}
		return param
	},
};

export default { mutations, state, actions, getters };