import store from "@/store";
const onlyAuth = (to, from, next) => {
	if (!store.getters.isAuthenticated) {
		// 아직 로그인이 안 되있는 유저니까 막아야 한다.
		next("/login");
	} else {
		next();
	}
};

export default [
	{
		meta: {
			title: 'TabletMain'
		},
		path: "/tabletMain",
		name: "TabletMain",
		beforeEnter: onlyAuth,
		component: () => import("@/views/tablet/TabletMain.vue")
	},
	{
		meta: {
			title: 'Tablet1'
		},
		path: "/tablet1",
		name: "Tablet1",
		beforeEnter: onlyAuth,
		component: () => import("@/views/tablet/Tablet1.vue")
	},
	{
		meta: {
			title: 'TabletList'
		},
		path: "/tabletList",
		name: "TabletList",
		beforeEnter: onlyAuth,
		component: () => import("@/views/tablet/TabletList.vue")
	},
	{
		meta: {
			title: 'Tablet2'
		},
		path: "/tablet2",
		name: "Tablet2",
		beforeEnter: onlyAuth,
		component: () => import("@/views/tablet/Tablet2.vue")
	},
	{
		meta: {
			title: 'Tablet3'
		},
		path: "/tablet3",
		name: "Tablet3",
		beforeEnter: onlyAuth,
		component: () => import("@/views/tablet/Tablet3.vue")
	},
];